import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag: 1,
			showFlag: 0,
			pageName: '暂态消息详情',
			radioFlag: 0,
			eventDetailIndex: '',
			errorFlag: 0,
			baseInfo: {},
			eigenvalueList: [],
			wave: {},
			sendAgain: '更新',
			second: 60,
			sendAgainFlag: 0,
			backNumber:0,
			eventMsgIndex:'',
			scrollHeight:'',
			loadingFlag:false,
			disabledFlag:false,
			judgeFlag:0,
			sagReason:''
		}
	},
	methods: {
		scrollH(){
			var that =this;
			var sys = uni.getSystemInfoSync();
			var winWidth = sys.windowWidth;
			var winrate = 750/winWidth;
			var winHeight= sys.windowHeight;
			var statusBarHeight = sys.statusBarHeight;
			that.scrollHeight = parseInt((winHeight-statusBarHeight)*winrate-94)+'rpx';
		},
		changeRadioFlag(flag) {
			this.radioFlag = flag;
		},
		confirmJudge(judgeFlag){
			var that = this;
			var content = '';
			if(uni.getStorageSync('userInfo').roleCode!=0){
				uni.showModal({
					title: '提示',
					content: '您暂无评价权限',
					showCancel: false
				})
				return;
			}
			if(judgeFlag==0){
				content='确认对设备无影响？'
			}else{
				content='确认对设备有影响？'
			}
			uni.showModal({
				title:'提示',
				content: content,
				success: function (res) {
					if (res.confirm) {
						if(that.judgeLogin()){
							uni.request({
								url: that.serverUrl+'/eventmsg/eventDetailEvaluate',
								method: 'POST',
								data: {
									eventDetailIndex: that.eventDetailIndex,
									evaluate: judgeFlag,
									userId: uni.getStorageSync('userInfo').userId
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded'
								},
								sslVerify: false,
								success: (res) => {
									uni.hideLoading();
									if (res.data.resultCode == '10000') {
										uni.showModal({
											title: '提示',
											content: '评价更新成功',
											showCancel: false
										})
										that.judgeFlag= judgeFlag;
									} else {
										uni.showModal({
											title: '提示',
											content: '评价更新失败，请重试',
											confirmText: '重试',
											success: function(res) {
												if (res.confirm) {
													that.updateEvaluate();
												} else if (res.cancel) {
													console.log('用户点击取消');
												}
											}
										})
									}
								},
								fail: (re) => {
									uni.showModal({
										title: '提示',
										content: '网络异常，请检查网络后重试',
										showCancel:false,
										success: function(res) {
											if (res.confirm) {
												console.log('用户点击确定');
											} else if (res.cancel) {
												console.log('用户点击取消');
											}
										}
									});
								}
							});
						}	
					} else if (res.cancel) {
						return;
					}
				}
			});
		},
		eventDetailBaseInfo() {
			var that = this;
			uni.request({
				url: that.serverUrl+'/eventmsg/eventDetailBaseInfo',
				method: 'POST',
				data: {
					eventMsgIndex: that.eventMsgIndex
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				sslVerify: false,
				success: (res) => {
					if (res.data.resultCode == '10000') {
						that.baseInfo = eval(res.data.data);
						that.baseInfo.showTime = that.formatTime_mills(that.baseInfo.timeID);
						// that.radioFlag = that.baseInfo.evaluate;
						that.judgeFlag = that.baseInfo.evaluate;
						uni.hideLoading();
					} else {
						that.errorFlag = 1
						uni.hideLoading();
					}
				},
				fail: (re) => {
					uni.hideLoading();
					that.errorFlag = 1
				}
			});
		},
		eventDetailEigenvalue() {
			var that = this;
			uni.request({
				url: that.serverUrl+'/eventmsg/eventDetailEigenvalue',
				method: 'POST',
				data: {
					eventDetailIndex: that.eventDetailIndex
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				sslVerify: false,
				success: (res) => {
					if (res.data.resultCode == '10000') {
						that.backNumber=res.data.backNumber;
						that.sagReason=res.data.sagReason;
						that.eigenvalueList = eval(res.data.data);
						uni.hideLoading();
					} else {
						that.errorFlag = 1
						uni.hideLoading();
					}
				},
				fail: (re) => {
					that.errorFlag = 1
					uni.hideLoading();
				}
			});
		},
		eventDetailWave() {
			var that = this;
			uni.request({
				url: that.serverUrl+'/eventmsg/eventDetailWave',
				method: 'POST',
				data: {
					eventDetailIndex: that.eventDetailIndex
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				sslVerify: false,
				success: (res) => {
					if (res.data.resultCode == '10000') {
						that.wave = eval(res.data.data);
						uni.hideLoading();
					} else {
						that.errorFlag = 1
						uni.hideLoading();
					}
				},
				fail: (re) => {
					uni.hideLoading();
					that.errorFlag = 1
				}
			});
		},
		updateEvaluate() {
			var that = this;
			if(that.judgeLogin()){
				that.sendAgainFlag = 1
				uni.request({
					url: that.serverUrl+'/eventmsg/eventDetailEvaluate',
					method: 'POST',
					data: {
						eventDetailIndex: that.eventDetailIndex,
						evaluate: that.radioFlag,
						userId: uni.getStorageSync('userInfo').userId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					sslVerify: false,
					success: (res) => {
						uni.hideLoading();
						if (res.data.resultCode == '10000') {
							uni.showModal({
								title: '提示',
								content: '评价更新成功',
								showCancel: false
							})
						} else {
							uni.showModal({
								title: '提示',
								content: '评价更新失败，请重试',
								confirmText: '重试',
								success: function(res) {
									if (res.confirm) {
										that.updateEvaluate();
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							})
						}
					},
					fail: (re) => {
						uni.showModal({
							title: '提示',
							content: '网络异常，请检查网络后重试',
							showCancel:false,
							success: function(res) {
								if (res.confirm) {
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
					}
				});
				if (that.second != 60) {
					return;
				}
				that.second--;
				var intervalID = setInterval(function() {
					that.sendAgain = '更新(' + that.second-- + "S)";
				}, 1000);
				setTimeout(function() {
					clearInterval(intervalID);
					that.sendAgain = "更新";
					that.second = 60;
					that.sendAgainFlag = 0
				}, 61000)
			}	
		},
		applyReport() {
			var that = this;
			if(that.judgeLogin()){
				that.disabledFlag=true;
				that.loadingFlag=true;
				uni.request({
					url: that.serverUrl+'/report/eventDetailReportApply',
					method: 'POST',
					data: {
						eventDetailIndex: that.eventDetailIndex,
						userId: uni.getStorageSync('userInfo').userId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					sslVerify: false,
					success: (res) => {
						that.loadingFlag=false;
						that.disabledFlag=false;
						if (res.data.resultCode == '10000') {
							uni.showModal({
								title: '提示',
								content: '申请报告成功',
								showCancel: false
							})
							that.eventDetailBaseInfo();
						} else {
							uni.showModal({
								title: '提示',
								content: that.showError(res.data.msgCode,res.data.msg),
								confirmText: '重试',
								success: function(res) {
									if (res.confirm) {
										that.applyReport();
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							})
						}
					},
					fail: (re) => {
						that.loadingFlag=false;
						that.disabledFlag=false;
						uni.showModal({
							title: '提示',
							content: '网络异常，请检查网络后重试',
							showCancel:false,
							success: function(res) {
								if (res.confirm) {
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
					}
				});
			}	
		},
		downloadReport() {
			var that = this;
			if(that.judgeLogin()){
				uni.request({
					url: that.serverUrl+'/report/eventDetailReportDownLoad',
					method: 'POST',
					data: {
						eventDetailIndex: that.eventDetailIndex,
						userId: uni.getStorageSync('userInfo').userId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					sslVerify: false,
					success: (res) => {
						uni.hideLoading();
						if (res.data.resultCode == '10000') {
							uni.showLoading({
								title:'正在下载',
								mask:true
							})
							uni.downloadFile({
								url: res.data.data.reportUrl,
								success: (r) => {
									uni.hideLoading();
									if (r.statusCode === 200) {
										uni.saveFile({
											tempFilePath: r.tempFilePath,
											success: function(res) {
												 var savedFilePath = res.savedFilePath;
												uni.showModal({
													title: '下载报告成功',
													// content: '文档保存在：jxkj/CanNengApp/apps/'+savedFilePath,
													content: '下载成功，是否直接打开',
													success: function(re) {
														if (re.confirm) {
															 uni.openDocument({
															      filePath: r.tempFilePath
															    });
														} else if (res.cancel) {
															console.log('用户点击取消');
														}
													}
												});
											},
											fail() {
												uni.showModal({
													title: '提示',
													content: '下载报告失败，请重试',
													confirmText: '重试',
													success: function(res) {
														if (res.confirm) {
															that.downloadReport();
														} else if (res.cancel) {
															console.log('用户点击取消');
														}
													}
												})
											}
										});
										
									} else {
										uni.showModal({
											title: '提示',
											content: '下载报告失败，请重试',
											confirmText: '重试',
											success: function(res) {
												if (res.confirm) {
													that.downloadReport();
												} else if (res.cancel) {
													console.log('用户点击取消');
												}
											}
										})
									}
								},
								fail() {
									uni.hideLoading();
									uni.showModal({
										title: '提示',
										content: '下载报告失败，请重试',
										confirmText: '重试',
										success: function(res) {
											if (res.confirm) {
												that.downloadReport();
											} else if (res.cancel) {
												console.log('用户点击取消');
											}
										}
									})
								}
							});
						} else {
							uni.showModal({
								title: '提示',
								content: '下载报告失败，请重试',
								confirmText: '重试',
								success: function(res) {
									if (res.confirm) {
										that.downloadReport();
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							})
						}
					},
					fail: (re) => {
						uni.showModal({
							title: '提示',
							content: '网络异常，请检查网络后重试',
							showCancel:false,
							success: function(res) {
								if (res.confirm) {
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
					}
				});
			}	
		},
		previewPic(url) {
			uni.previewImage({
				urls: [url],
				current:0,
				longPressActions: {
					itemList: ['保存图片'],
					success: function(data) {
						if (data.tapIndex == 0) {
							uni.showLoading({
								title: "图片保存中"
							});
							uni.downloadFile({
								url: url,
								success(result) {
									var tempFilePath = result.tempFilePath;
									uni.saveImageToPhotosAlbum({
										filePath: tempFilePath,
										success() {
											uni.showToast({
												icon: "success",
												title: "保存成功",
												duration: 2000
											})
										}
									})
								},
								complete() {
									uni.hideLoading();
								}
							})
						}
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		retry(){
			this.errorFlag=0;
			this.eventDetailBaseInfo();
			this.eventDetailEigenvalue();
			this.eventDetailWave();
		}
	},
	onLoad(e) {
		this.scrollH();
		uni.showLoading({
			mask:true,
		});
		this.eventDetailIndex = e.eventDetailIndex;
		this.eventMsgIndex = e.eventMsgIndex;
		this.eventDetailBaseInfo();
		this.eventDetailEigenvalue();
		this.eventDetailWave();
	},
	components: {
		back
	}
}
